"use client"

import { useState } from 'react';

export default function LikeDislikeComment() {
  const [liked, setLiked] = useState(false);
  const [disliked, setDisliked] = useState(false);
  const [comment, setComment] = useState('');
  const [submittedComment, setSubmittedComment] = useState('');

  const handleLike = () => {
    setLiked(!liked);
    if (disliked) setDisliked(false);
  };

  const handleDislike = () => {
    setDisliked(!disliked);
    if (liked) setLiked(false);
  };

  const handleCommentSubmit = (e) => {
    e.preventDefault();
    setSubmittedComment(comment);
    setComment('');
  };

  return (
    <div style={{ fontFamily: 'Arial', maxWidth: '400px', margin: '20px auto' }}>
      <div style={{ fontSize: '24px', marginBottom: '10px' }}>
        <button onClick={handleLike} style={{ color: liked ? 'green' : 'gray' }}>
          üëç Like
        </button>
        <button
          onClick={handleDislike}
          style={{ color: disliked ? 'red' : 'gray', marginLeft: '10px' }}
        >
          üëé Dislike
        </button>
      </div>

      <form onSubmit={handleCommentSubmit}>
            <textarea
                placeholder="Write a comment..."
                value={comment}
                onChange={(e) => setComment(e.target.value)}
                rows={4}
                style={{ width: '100%', padding: '8px', borderRadius: '4px', resize: 'none' }}
            />
            <button
            type="submit"
            style={{
                marginTop: '8px',
                padding: '6px 12px',
                backgroundColor: '#0070f3',
                color: 'white',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer'
            }}
            >
            Submit Comment
            </button>
      </form>

      {submittedComment && (
        <div style={{ marginTop: '20px', background: '#f3f3f3', padding: '10px', borderRadius: '4px' }}>
          <strong>Comment:</strong>
          <p>{submittedComment}</p>
        </div>
      )}
    </div>
  );
}
